<template>
  <div class="article-list-container">
    <div class="search-bar">
      <el-input
        v-model="searchQuery"
        placeholder=""
        prefix-icon="el-icon-search"
        clearable
        @input="handleSearch"
      >
        <template #append>
          <el-button @click="handleSearch"></el-button>
        </template>
      </el-input>
      
      <div class="filter-options">
        <el-select v-model="selectedCategory" placeholder="" @change="handleCategoryChange">
          <el-option label="" value=""></el-option>
          <el-option
            v-for="category in categories"
            :key="category.id"
            :label="category.name"
            :value="category.id"
          ></el-option>
        </el-select>
        
        <el-select v-model="sortOption" placeholder="" @change="handleSortChange">
          <el-option label="" value="newest"></el-option>
          <el-option label="" value="most-read"></el-option>
          <el-option label="" value="most-comments"></el-option>
        </el-select>
      </div>
    </div>
    
    <div class="article-list">
      <el-empty v-if="articles.length === 0" description=""></el-empty>
      
      <ArticleCard
        v-for="article in articles"
        :key="article.id"
        :article="article"
        @click="viewArticle(article.id)"
      />
    </div>
    
    <div class="pagination-container">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="pageSize"
        :current-page="currentPage"
        @current-change="handlePageChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import ArticleCard from '@/components/blog/ArticleCard.vue'
import { fetchArticles } from '@/api/article'

export default {
  name: 'ArticleListView',
  components: {
    ArticleCard
  },
  setup() {
    const router = useRouter()
    const articles = ref([])
    const loading = ref(false)
    const total = ref(0)
    const currentPage = ref(1)
    const pageSize = ref(10)
    const searchQuery = ref('')
    const selectedCategory = ref('')
    const sortOption = ref('newest')
    
    // 
    const categories = [
      { id: '1', name: '' },
      { id: '2', name: '' },
      { id: '3', name: '' },
      { id: '4', name: '' }
    ]
    
    // 
    const loadArticles = async () => {
      loading.value = true
      try {
        // API
        const params = {
          page: currentPage.value,
          pageSize: pageSize.value,
          keyword: searchQuery.value,
          category: selectedCategory.value,
          sort: sortOption.value
        }
        
        // API
        setTimeout(() => {
          // 
          const mockArticles = Array.from({ length: 10 }, (_, index) => ({
            id: `${currentPage.value}-${index}`,
            title: ` ${currentPage.value}-${index}`,
            summary: '...',
            author: '',
            publishDate: new Date().toLocaleDateString(),
            category: categories[Math.floor(Math.random() * categories.length)].name,
            viewCount: Math.floor(Math.random() * 1000),
            commentCount: Math.floor(Math.random() * 50),
            coverImage: `https://picsum.photos/id/${index + 10}/300/200`
          }))
          
          articles.value = mockArticles
          total.value = 100 // 
          loading.value = false
        }, 500)
      } catch (error) {
        ElMessage.error('')
        loading.value = false
      }
    }
    
    // 
    const viewArticle = (id) => {
      router.push(`/article/${id}`)
    }
    
    // 
    const handleSearch = () => {
      currentPage.value = 1
      loadArticles()
    }
    
    // 
    const handleCategoryChange = () => {
      currentPage.value = 1
      loadArticles()
    }
    
    // 
    const handleSortChange = () => {
      currentPage.value = 1
      loadArticles()
    }
    
    // 
    const handlePageChange = (page) => {
      currentPage.value = page
      loadArticles()
    }
    
    onMounted(() => {
      loadArticles()
    })
    
    return {
      articles,
      loading,
      total,
      currentPage,
      pageSize,
      searchQuery,
      selectedCategory,
      sortOption,
      categories,
      viewArticle,
      handleSearch,
      handleCategoryChange,
      handleSortChange,
      handlePageChange
    }
  }
}
</script>

<style scoped>
.article-list-container {
  padding: 20px;
}

.search-bar {
  margin-bottom: 20px;
}

.filter-options {
  display: flex;
  margin-top: 15px;
  gap: 15px;
}

.article-list {
  margin-bottom: 20px;
}

.pagination-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
</style>
